*{
	-webkit-box-sizing: border-box;
	-ms-box-sizing: border-box;
	box-sizing: border-box;
}
html{
	font-family: sans-serif;
	-webkit-text-size-adjust:100%;
	-ms-text-size-adjust:100%
}
html,body{
	position: relative;
	width: 100%;
	height: 100%;
}
body{
  font-size: 14px;
  font-family:"Microsoft Yahei","Hiragino Sans GB","Helvetica Neue",Helvetica,tahoma,arial,Verdana,sans-serif,"WenQuanYi Micro Hei","\5B8B\4F53";
  background-color: #F5F5F5;
  color: #333;
}
html,body,h1,h2,h3,h4,h5,h6,hr,p,iframe,
dl,dt,dd,ul,ol,li,pre,form,button,input,
textarea,th,td,fieldset{margin:0;padding:0}
ul,ol,dl{list-style-type:none}
address,caption,cite,code,dfn,em,th,var{font-style:normal;font-weight:400}
input,button,textarea,select,optgroup,
option{font-family:inherit;font-size:inherit;font-style:inherit;font-weight:inherit}
input,button{overflow: visible;vertical-align:middle;outline:none}
button,input{border:none;}
body,th,td,button,input,select,
textarea{-webkit-font-smoothing: antialiased;-moz-osx-font-smoothing:grayscale}
body{margin: auto;}
th,td,button,input,
select,textarea{font-family:"Microsoft Yahei","Hiragino Sans GB","Helvetica Neue",Helvetica,tahoma,arial,Verdana,sans-serif,"WenQuanYi Micro Hei","\5B8B\4F53";}
div,p{word-wrap:break-word}
a,area{outline:none;blr:expression(this.onFocus=this.blur())}
a{text-decoration:none;cursor: pointer;color: #333}
a:hover{text-decoration:underline;outline:none;}
a:focus{outline:none}
a:hover,a:active{outline:none;text-decoration: none;}
:focus{outline:none}
sub,sup{vertical-align:baseline}
/*button,input[type="button"], input[type="submit"] {line-height:normal!important;}*/
/*input:focus{border: none;}*/
img{border:0;vertical-align:middle;width: 100%;height: 100%;}
a img,img{-ms-interpolation-mode:bicubic}
*html{overflow:-moz-scrollbars-vertical;zoom:expression(function(ele){ele.style.zoom = "1";document.execCommand("BackgroundImageCache",false,true)}(this))}
/*HTML5 reset*/
header,footer,section,aside,details,
menu,article,section,nav,address,
hgroup,figure,figcaption,legend{display:block;margin:0;padding:0}
time{display:inline}
audio,canvas,video{display:inline-block;*display:inline;*zoom:1}
audio:not([controls]){display:none}
legend{width:100%;margin-bottom:20px;font-size:21px;line-height:40px;border:0;border-bottom:1px solid #e5e5e5}
legend small{font-size:15px;color:#999}
svg:not(:root){overflow: hidden}
fieldset {border-width:0;padding: 0.35em 0.625em 0.75em;margin: 0 2px;border: 1px solid #c0c0c0}
input[type="number"]::-webkit-inner-spin-button,input[type="number"]::-webkit-outer-spin-button {height: auto}
input[type="search"] {-webkit-appearance: textfield; /* 1 */-webkit-box-sizing: content-box; /* 2 */box-sizing: content-box}
input[type="search"]::-webkit-search-cancel-button,input[type="search"]::-webkit-search-decoration {-webkit-appearance: none}
input::-ms-clear{display: none;}
textarea{resize: none;border:none;}
/*去掉苹果对textarea的默认样式*/
textarea ,input{/*-webkit-appearance: none;*/border-radius:0px;font-family:Microsoft YaHei}
/*清除a,button,input手机端的点击效果*/
a,button,input{-webkit-tap-highlight-color:rgba(255,0,0,0);}
/*将设备自动添加的播放按钮隐藏*/
video::-webkit-media-controls {
    display:none !important;
}
video{width: 100%;height: 100%;background:#000;}


/*居中*/
.cont{
	position: absolute;
	width: 100%;
	top: 50%;
	-webkit-transform: translateY(-50%);
	        transform: translateY(-50%);
}
/*显示隐藏*/
.hide{display: none;}
.show{display: block;}
/*浮动*/
.fl{float: left;}
.fr{float: right;}
/*清除浮动*/
.clearfix{ zoom:1; }
.clearfix:after{display: block;visibility: hidden;voerflow: hidden;content: ".";clear: both;height: 0;}
/*文本溢出*/
.wbyc{overflow: hidden;text-overflow:ellipsis;white-space: nowrap;}
.wbyc2{display: -webkit-box;-webkit-box-orient: vertical;-webkit-line-clamp: 2;overflow: hidden;}
.wbyc3{display: -webkit-box;-webkit-box-orient: vertical;-webkit-line-clamp: 3;overflow: hidden;}
.wbyc4{display: -webkit-box;-webkit-box-orient: vertical;-webkit-line-clamp: 4;overflow: hidden;}
.wbyc5{display: -webkit-box;-webkit-box-orient: vertical;-webkit-line-clamp: 5;overflow: hidden;}
/*文字对齐*/
.t_c{text-align: center;}
.t_r{text-align: right;}
.t_l{text-align: left;}
/*透明*/
.opacity{filter: alpha(opacity=0);-moz-opacity: 0; -khtml-opacity: 0;opacity: 0;}
.opacity1{filter: alpha(opacity=.1);-moz-opacity: .1; -khtml-opacity: .1;opacity: .1;}
.opacity2{filter: alpha(opacity=.2);-moz-opacity: .2; -khtml-opacity: .2;opacity: .2;}
.opacity4{filter: alpha(opacity=.4);-moz-opacity: .4; -khtml-opacity: .4;opacity: .4;}
.opacity6{filter: alpha(opacity=.6);-moz-opacity: .6; -khtml-opacity: .6;opacity: .6;}
/*滚动条*/
.over_y{overflow-y: auto;}
.over_x{overflow-x: auto;white-space: nowrap;}
.over{overflow: auto;}
.over_hidden{overflow: hidden;}
/*删除线*/
.scx{text-decoration:line-through;}


